    background: white;
}

.sprotty-node {
    fill: #cdc;
    stroke: rgb(0, 0, 0);
}

.sprotty-edge {
    stroke: black;
}

.sprotty-edge.arrow {
    fill: black;
}

.sprotty-edge.selected {
    stroke: #844;
}

.sprotty-edge.selected > .arrow {
    fill: #844;
    stroke: #844;
}

.sprotty-node:not(.selected) {
    stroke: black;
}

.forkOrJoin > .sprotty-node {
    fill: black;
}

.forkOrJoin > .sprotty-node.selected {
    stroke: rgb(87, 87, 214);
}

polygon.sprotty-node {
    stroke: black;
}

.sprotty-node.selected {
    stroke: rgb(87, 87, 214);
}
